/* OVERWRITING Diagram.js */
.djs-palette {
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2) !important;
}

/* General */
button {
  border: none;
  background: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

button:hover,
a:hover {
  cursor: pointer;
}

.pjs-error {
  color: #ff0000;
}

.pjs-visible {
  display: block !important;
}

.pjs-first {
  /* DONT CHANGE. USED AS PSEUDOCLASS */
}

.pjs-buttons {
  position: fixed;
}

.pjs-buttons > ul {
  display: block;
  padding: 0;
  margin: 0;
  margin-top: 10px;
  list-style: none;
}

.pjs-buttons > ul > li {
  display: inline-block;
  margin-right: 10px;
}

.pjs-buttons > ul > li > a,
.pjs-buttons button {
  color: black;
  background-color: #f8f8f8;
  border-radius: 3px;
  display: inline-block;
  padding: 6px 10px;
  font-size: 13px;
  font-weight: bold;
  text-align: center;
  border: solid 1px #cdcdcd;
}

.pjs-buttons button:hover {
  cursor: pointer;
}

.pjs-buttons a {
  opacity: 0.3;
  text-decoration: none;
}

.pjs-button-disabled,
.pjs-button-disabled:hover,
.pjs-button-disabled:active {
  opacity: 0.3;
  text-decoration: none;
}

.pjs-buttons a.pjs-buttons-active,
.pjs-buttons button.pjs-buttons-active {
  opacity: 1;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1);
}

/* tool tip */

.pjs-tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black;
}

.pjs-tooltip .pjs-tooltiptext {
  font-family: "IBM Plex Sans", sans-serif;
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 20px;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-left: -60px;

  /* Fade in tooltip - takes 1 second to go from 0% to 100% opac: */
  opacity: 0;
  transition: opacity 1s;
}

.pjs-tooltip:hover .pjs-tooltiptext {
  visibility: visible;
  opacity: 1;
}

.pjs-tooltip .pjs-tooltiptext::after {
  content: " ";
  position: absolute;
  top: 100%; /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

/* canvas drawing */

.pjs-text-box:before {
  content: "Abc";
  font-size: medium;
}

.pjs-horizontal {
  display: inline-block;
}

.pjs-horizontal li {
  float: left;
}

[class*=" pjs-general-icon"]::before {
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
}

.pjs-general-icon {
  font-family: "Font Awesome 5 Free Solid", serif;
  line-height: 1.2em;
}

.pjs-ui-element-bordered {
  color: black;
  background-color: #f8f8f8;
  border-radius: 3px;
  display: inline-block;
  font-size: 13px;
  border: solid 1px #cdcdcd;
}

.pjs-io-dialog-text-hint {
  margin-top: 4px;
  font-size: 13px;
  text-align: left;
  color: #555555;
}

.pjs-io-dialog-text-hint .pjs-tooltip .pjs-tooltiptext {
  margin-left: -80px !important;
}

/* flex box grid */

.pjs-row {
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 0;
  -ms-flex: 0 1 auto;
  flex: 0 1 auto;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -ms-flex-wrap: wrap-reverse;
  flex-wrap: wrap-reverse;
}

.pjs-col-xs {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  max-width: 100%;
  box-sizing: border-box;
}

.pjs-box,
.pjs-box-first,
.pjs-box-large,
.pjs-box-nested,
